import jsPDF from "jspdf";
import "jspdf-autotable";
import { getUserdata } from "../../../components/helpers/function";
import img from "../../../assets/img/POUSKOPPOLDA.png";
import angkaTerbilang from "@develoka/angka-terbilang-js";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const ModuleReprintNotaHutang = (data = "", kode_baki = "") => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [218,140]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  // let footRows = [];
  let tableColumn = [];
  let finalY = 60;

  data.forEach((row,index) => {
    if (index !== 0){
      doc.addPage()
    }
    doc.setFontSize(10);
    doc.addImage(img, "png", 14, 10, 20, 20);
    doc.text(
      90,
      15,
      "PUSAT KOPRASI KEPOLISIAN RI\n DAERAH JAWA TIMUR\n (POUSKOPPOLDA JATIM)",
      "center"
    );
    doc.text(
      90,
      30,
      "Badan Hukum No. 6067 B / BH / II / 1969\nTanggal 8 Juli 1985 ",
      "center"
    );
    doc.setFontSize(10);
    doc.text(
      `UNIT ANEKA USAHA\nTOKO EMAS ${getUserdata().nama_toko}`,
      170,
      15,
      "center"
    );

    let jml_alamat = getUserdata().alamat_toko.length;
    if (jml_alamat > 10) {
      doc.text(getUserdata().alamat_toko.slice(0, 48), 145, 30);
    }
    if (jml_alamat > 50) {
      doc.text(getUserdata().alamat_toko.slice(48, 90), 145, 30);
    }
    doc.setFontSize(10);
    doc.setProperties({
      title: "REPRINT NOTA HUTANG",
    });
    doc.text(
      14,
      35,
      "_______________________________________________________________________________________________"
    );

    doc.setFontSize(10);
    doc.text(14, 43, "TANGGAL");
    doc.text(35, 43, `: ${row.tgl_system}`);
    doc.text(14, 48, "NILAI");
    doc.text(35, 48, `: ${row.jumlah_hutang.toLocaleString("kr-KO")}`);
    doc.text(90, 43, `No.${row.no_faktur_hutang}`);
    doc.text(140, 43, "Nama");
    doc.text(152, 43, `: ${row.nama_customer}`);
    doc.text(140, 48, "Alamat");
    doc.text(152, 48, `: ${row.n_alamat_customer}`);
    doc.text(140, 53, "Telp");
    doc.text(152, 53, `: ${row.no_hp}`);
    doc.text(90, 53, "Sales");
    doc.text(100, 53, `: ${row.kode_sales}`);

    tableColumn = [
      [
        {
          content: `NAMA BARANG`,
          styles: {
            halign: "center",
          },
        },
        {
          content: `KADAR`,
          styles: {
            halign: "center",
          },
        },
        {
          content: `BERAT`,
          styles: {
            halign: "center",
          },
        },
      ],
    ];

    let totberat = 0;
    row.detail_barang.forEach((element) => {
      totberat = totberat + element.berat;
      let row = [
        element.nama_barang,
        {
          content: element.kadar || "-",
          styles: {
            halign: "center",
          },
        },
        {
          content: `${element.berat} GR`,
          styles: {
            halign: "center",
          },
        },
      ];
      tableColumn.push(row);
    });

    doc.setFontSize(10);
    doc.text(14, 100, "Jumlah");
    doc.text(30, 100, `: Rp: ${row.jumlah_hutang.toLocaleString("ko-Ko")}`);
    doc.text(14, 105, "Terbilang");
    doc.text(30, 105, `: ${angkaTerbilang(row.jumlah_hutang)}`);
    doc.text(150, 105, "Total Berat");
    doc.text(170, 105, `: ${totberat} Gr`);
    doc.text(
      14,
      107,
      "_______________________________________________________________________________________________"
    );
    doc.setFontSize(10);
    doc.text(14, 112, "Keterangan");
    doc.text(
      14,
      118,
      "Apabila titipan (jaminan) tidak bisa di beli kembali dalam waktu 3(tiga)\nbulan atau tidak dibayar dari keuntungannya, maka pihak UNIT ANEKA USAHA\nTOKO EMAS SUMBER URIP berhak menjual barang-barang tersebut tanpa\npemberitahuan kepada pemilik"
    );
    doc.text(169, 112, "Peminjam");
    doc.text(165, 130, "(.........................)");
    doc.setFontSize(8);
    doc.text(135, 130, `Tgl Cetak : ${row.tgl_system}`);
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      // foot: footRows,
      startY: finalY,
      theme: "plain",
      rowPageBreak: "avoid",
      // pageBreak: "avoid",
      margin: { top: 10 },
      bodyStyles: {
        // lineWidth: 0.01,
        // lineColor: "#000",
        fontSize: 8,
      },
      headStyles: {
        fontSize: 8,
        lineWidth: 0.01,
        lineColor: "#000",
        //   textColor: "#000",
        //   fillColor: "#E8E5E5",
      },
    });
  });

  
  tableRows = [];

  // const pages = doc.internal.getNumberOfPages();
  // const pageWidth = doc.internal.pageSize.width; //Optional
  // const pageHeight = doc.internal.pageSize.height; //Optional

  
 
  doc.setFontSize(10); //Optional
  // for (let j = 1; j < pages + 1; j++) {
  //   let horizontalPos = pageWidth / 2; //Can be fixed number
  //   let verticalPos = pageHeight - 10; //Can be fixed number
  //   doc.setPage(j);
  //   // doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
  //   //   align: "center",
  //   // });
  // }

  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN  BARANG DETAIL</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  x.document.close();
};

export default ModuleReprintNotaHutang;
